import * as React from "react"
import { Link } from "gatsby"
import BookTemplate1 from "@components/common/Books/BookTemplate1"

const Book = ({ book, position }) => (
    <div className="row mb-5">

        <div className="col-md-6">
            <div className="d-flex justify-content-center">
                <Link to={ book.link }>
                    <BookTemplate1 cover={ book.cover } />
                </Link>
            </div>
        </div>

        <div
            className={ 'col-md-6 pt-5 '
                + (
                    (position || 0) % 2 == 0
                        ? 'order-sm-first text-sm-right'
                        : ''
                )
            }>
            <div className="section-title mb-4">
                <h2 className="title lh-sm">
                    { book.title }
                </h2>
                <h3 className="fs-1">
                    {/* <b> { book.subtitle } </b> */ }
                    { book.subtitle }
                </h3>
            </div>
            <h3 className="fs-1 text-muted">
                by { book.author }
            </h3>
        </div>
    </div>
)
export default Book
